// COLORS
$white:          #FFF;
$black:          #000;
$transparent:    transparent;

$charcoal:       #444;

$gray-tint-7:    #E7E8EA;
$gray-tint-6:    #F8F9FA;
$gray-tint-5:    #F5F6F9;
$gray-tint-4:    #F3F4F7;
$gray-tint-45:   #E7E7E7;
$gray-tint-3:    #dadddf; //lighten($gray, 25%);
$gray-tint-2:    #cdd0d3; //lighten($gray, 20%);
$gray-tint-1:    #b1b7bc; //lighten($gray, 10%);
$gray-tint-05:   #A1A9AD;
$gray:           #969EA4;
$gray-shade-1:   #8C969D;
$gray-shade-2:   #7b858C; //darken($gray, 10%);
$gray-shade-4:   #646D73;
$gray-shade-45:  #52585b;
$gray-shade-5:   #3C4A54; //darken($gray, 20%);
$gray-shade-6:   #22303A;
$gray-shade-7:   #3B4A54;

$red-tint-3:     #F85A45;
$red-tint-1:     #E74528;
$red:            #D32D27;
$red-shade-3:    #AF0809;
$red-shade-6:    #690124;

$orange:  #F39C12;

$green-tint-3:   #99E891;
$green:          #2ECC71;
$green-shade-3:  #18A279;
$green-shade-6:  #0B6958;
$green-shade-9:  #5fd889;
$green-shade-11: #39ca73;

$yellow-tint-3:  #EFC20F;
$yellow:         #F39C12;
$yellow-shade-3: #E67E22;
$yellow-shade-6: #D35400;

$blue-tint-3:    #7CB8E1;
$blue:           #3498DB;
$blue-shade-3:   #2980B9;
$blue-shade-6:   #194E70;

$aqua-tint-3:    #76c8e9; //lighten($aqua, 10%);
$aqua:           #4AB7E2;
$aqua-shade-3:   #22a4d7; //darken($aqua, 10%);

$poor-meter:     #F39C12;
$bad-meter:      #EFC20F;
$ok-meter:       #D6CF40;
$good-meter:     #C0C75F;
$great-meter:    #9DC96E;
$best-meter:     #79CC97;

$link-color: $red;
$link-hover: $red-tint-3;

// SHADOWS
$shadow-z1: 0 1px 2px rgba($gray-shade-5, .3);
$shadow-z2: 0 2px 3px rgba($gray-shade-5, .3);
$shadow-z3: 0 3px 4px rgba($gray-shade-5, .3);

$shadow-border-inner: inset 0 0 0 1px $gray-tint-1;

// FONTS
$font-default: 'Open Sans', sans-serif;

// SIZES
$font-size-root:  16px;
$font-size-base:  1rem; //16px

$font-size-h1:    2.250 * $font-size-base;  //36px
$font-size-h2:    1.850 * $font-size-base;  //29.6px
$font-size-h3:    1.500 * $font-size-base;  //24px
$font-size-h4:    1.250 * $font-size-base;  //20px
$font-size-h5:    1.100 * $font-size-base;  //17.6px
$font-size-h6:            $font-size-base;  //16px
$font-size-h7:    0.938 * $font-size-base;  //15px
$font-size-sm:    0.900 * $font-size-base;  //14.4px
$font-size-xs:    0.813 * $font-size-base;  //13px
$font-size-xxs:   0.750 * $font-size-base;  //12px
$font-size-xxxs:  0.688 * $font-size-base;  //11px

// LINE HEIGHT
$line-height-base: 1.4 * 1rem; //22.4px

$line-height-h1:  2.250 * $line-height-base;
$line-height-h2:  1.850 * $line-height-base;
$line-height-h3:  1.500 * $line-height-base;
$line-height-h4:  1.250 * $line-height-base;
$line-height-h5:  1.100 * $line-height-base;
$line-height-h6:          $line-height-base;
$line-height-sm:  0.900 * $line-height-base;
$line-height-xs:  0.815 * $line-height-base;

$line-height-btn: $line-height-base - 0.5rem;

// WEIGHTS
$weight-light:    300;
$weight-normal:   400;
$weight-semibold: 600;
$weight-bold:     700;

// MARGINS and PADDING
$margin-base: 12px;

$margin-xl:   3.0 * $margin-base;
$margin-lg:   2.0 * $margin-base;
$margin-md:         $margin-base;
$margin-sm:   0.5 * $margin-base;

//BORDERS
$border-radius-base: 2px;

$border-mid: 1px solid #AFC6D6;

//Omar Tiles
$primary-info: #22303A;
$secondary-info: #535F69;
