@import '../../components/sso/newvault/styles/variables';

/*
******* Dialog override *******
*/
#introTutorialWelcomeDialog {
  min-height: 215px;
  max-width: 550px;
  min-width: 485px;
}
#introTutorialWelcomeDialog  .intro-tutorial.container {
  width: 520px;
  padding-right: 15px;
  padding-left: 15px;
  margin: 0;
}

#introTutorialSelectSiteDialog,
#introTutorialCompleteDialog {
  min-height: 215px;
  max-width: 620px;
  min-width: 485px;
}
#introTutorialWelcomeDialog .dialogContent,
#introTutorialSelectSiteDialog .dialogContent,
#introTutorialCompleteDialog .dialogContent {
  padding-bottom: 0;
  padding-top: 0;
  padding-left: 0;
  padding-right: 0;
}
.lp-grid.container.intro-tutorial {
  width: 590px;
}
/*
******* Intro Tutorial Dialogs *******
*/
.intro-tutorial {
  img {
    vertical-align: middle;
  }
  hr {
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: -15px;
    margin-right: -15px;
    border: 0;
    border-top: 1px solid #cdcdcd;
  }
  p {
    font-size: 13px;
    color: #3F3F3A;
  }
  a {
    text-decoration: none;
  }
  h1 {
    text-transform: capitalize;
    font-weight: lighter;
    font-size: 26px;
    line-height: 36px;
    color: #3F3F3A;
  }
  h2 {
    font-weight: 400;
    font-size: 22px;
    line-height: 36px;
  }
  h3 {
    font-weight: normal;
    font-size: 16px;
    line-height: 24px;
  }
  h4 {
    font-weight: 200;
    font-size: 16px;
    line-height: 32px;
    margin-top: 15px;
    margin-bottom: 15px;
  }
  h5 {
    font-weight: 200;
    font-size: 24px;
    line-height: 48px;
    margin-bottom: 5px;
  }
  .line-height-sm {
    line-height: 24px;
  }
  .line-height-md {
    line-height: 32px;
  }
  .line-height-lg {
    line-height: 42px;
  }
  .clickable {
    cursor: pointer;
  }
  .btn-cursor {
    cursor: pointer;
  }
  .btn {
    /* Structure */
    display: inline-block;
    padding: 18.5px 25px;
    width: 215px;
    border-radius: 3px;
    background: $white;
    font-weight: normal;
    box-shadow: 0 1px 2px rgba(34, 48, 58, .3);
    border: 1px solid #b6b6b6;
    zoom: 1;
    white-space: nowrap;
    vertical-align: middle;
    text-align: center;
    cursor: pointer;
    -webkit-user-drag: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    box-sizing: border-box;
    &::-moz-focus-inner {
      padding: 0;
      border: 0;
    }
  }
  .btn-primary {
    border: 1px solid transparent;
    color: $white;
    background: $red;
    box-shadow: 0 1px 2px rgba(34, 48, 58, .3);
    :hover{
      background: $red;
      color: $white;
    }
  }
  .btn-dark {
    border: 1px solid transparent;
    color: $white;
    background: #3F3F3A;
    box-shadow: 0 1px 2px rgba(34, 48, 58, .3);
    :hover{
      background: #3F3F3A;
      color: $white;
    }
  }
  .btn-close {
    width: 12px;
    height: 12px;
    margin-top: 15px;
  }
  .btn-short {
    padding: 11px 25px;
  }

  .bg-red {
    background-color: $red;
  }
  .bg-white {
    background-color: $white;
  }
  .bg-gray {
    background-color: #F0F2F3;
  }
  .bg-dark {
    background-color: #3F3F3A;
  }
  .congrats-header {
    background-image: url('../images/vault_4.0/congrats-confetti.png');
    background-position: 0 0;
    padding-bottom: 39px;
    font-weight: 100;
  }
  .congrats-footer {
    margin-top: 30px;
    margin-bottom: 15px;
  }
  .congrats h2 {
    font-weight: 600;
  }

  .txt-white {
    color: $white;
  }
  .txt-red {
    color: $red;
  }
  .txt-gray {
    color: #828282;
  }
  .txt-sm-transparent {
    color: rgba(255, 255, 255, .7);
  }
  .txt-dark {
    color: #3F3F3A;
  }

  .main-icon {
    height: 200px;
    margin-top: 10px;
    margin-bottom: 5px;
    margin-left: 45px;
  }
  .sm-icon {
    height: 15px;
    width: 15px;
  }

  .no-bottom-margin {
    margin-bottom: 0;
    -webkit-margin-after: 0;
    -webkit-margin-end: 0;
  }
  .no-top-margin {
    margin-top: 0;
    -webkit-margin-before: 0;
    -webkit-margin-start: 0;
  }
  .no-right-margin {
    margin-right: 0;
  }
  .no-left-margin {
    margin-left: 0;
  }
  .align-baseline {
    vertical-align: baseline;
  }
  .title-margin {
    padding-left: 0;
    margin-top: 30px;
    margin-left: -10px;
  }
  .tile-margin {
    margin-bottom: 20px;
  }

  .tile {
    .overlay-text {
      color: $white;
      position: absolute;
      top: 93px;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
      text-align: center;
      vertical-align: middle;
      background-color: $yellow-shade-3;
      line-height: 40px;
      font-size: 14px;
      font-weight: lighter;
    }
    .itemIcon {
      height:131px;
    }
    .launchButton {
      margin-top: 0;
      margin-bottom: 0;
      margin-left: auto;
      margin-right: auto;
      top: 24px;
    }
    &:hover, &.focus, &.selected {
      .itemInfo {
        display: none;
      }
    }
  }
  &.spotlight-cover {
    background-color: transparent;
    z-index: 8;
    width: 100%;
    height: 100%;
    position: absolute;
  }
}
